

import PropTypes from 'prop-types';

export default PropTypes.shape({
  id: PropTypes.number,

  projectName: PropTypes.string,
  projectId: PropTypes.string,
  procurementOfficerName: PropTypes.string,
  procurementContactNo: PropTypes.number,
});

export const formValues = PropTypes.shape({
  projectName: PropTypes.string,
  projectId: PropTypes.string,
  procurementOfficerName: PropTypes.string,
  procurementContactNo: PropTypes.oneOfType([PropTypes.string, PropTypes.number]),
});

export const formTouched = PropTypes.shape({
  projectName: PropTypes.oneOfType([PropTypes.bool, PropTypes.shape()]),
  projectId: PropTypes.oneOfType([PropTypes.bool, PropTypes.shape()]),
  procurementOfficerName: PropTypes.oneOfType([PropTypes.bool, PropTypes.shape()]),
  procurementContactNo: PropTypes.oneOfType([PropTypes.bool, PropTypes.shape()]),
});

export const formErrors = PropTypes.shape({
  projectName: PropTypes.oneOfType([PropTypes.string, PropTypes.shape()]),
  projectId: PropTypes.oneOfType([PropTypes.string, PropTypes.shape()]),
  procurementOfficerName: PropTypes.oneOfType([PropTypes.string, PropTypes.shape()]),
  procurementContactNo: PropTypes.oneOfType([PropTypes.string, PropTypes.shape()]),
});
